body {
  background: #1f1f37c8;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Roboto", sans-serif;
}

.pomodoro-container {
  max-width: 400px;
  width: 90%;
  margin: 40px auto;
  padding: 20px;
  background: #1e1e2f;
  color: #fff;
  border-radius: 12px;
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.3);
  text-align: center;
}

.pomodoro-container h2 {
  font-size: 1.2rem;
  margin-bottom: 10px;
  color: #f0f0f0;
}

.pomodoro-container h3 {
  font-size: 1.5rem;
  margin-bottom: 10px;
}

.break-container,
.session-container {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 15px;
  margin-bottom: 20px;
}

.break-container p,
.session-container p {
  font-size: 1.4rem;
  font-weight: bold;
}

.break-container button,
.session-container button {
  background: #2e2e3f;
  color: #fff;
  border: none;
  padding: 10px 14px;
  border-radius: 8px;
  font-size: 1.2rem;
  cursor: pointer;
  transition: background 0.3s ease;
}

.break-container button:hover,
.session-container button:hover {
  background: #3f3f5f;
}

#break-length,
#session-length {
  font-size: 1.4rem;
  margin: 0 10px;
}

.timer-container {
  margin-top: 20px;
  padding: 20px;
  background: #2a2a3f;
  border-radius: 10px;
}

.timer-container[style*="color: red"] #time-left {
  animation: almost-done 1s infinite;
}

@keyframes almost-done {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.1);
  }
  100% {
    transform: scale(1);
  }
}

#time-left {
  display: block;
  font-size: 3rem;
  font-weight: bold;
  margin: 10px 0;
}

.timer-container[style*="color: red"] {
  color: #ff4d4d;
}
.timer-container[style*="color: green"] {
  color: #4dff88;
}

.control-buttons {
  display: flex;
  justify-content: center;
  gap: 15px;
  margin-top: 15px;
}

.control-buttons button {
  background: #6262dc;
  color: #fff;
  border: none;
  padding: 12px 18px;
  border-radius: 8px;
  font-size: 1rem;
  cursor: pointer;
  transition: background 0.3s ease;
}

.control-buttons button:hover {
  background: #6d6dff;
}

.control-buttons #reset {
  background: #ff4d4d;
}

.control-buttons #reset:hover {
  background: #ff6d6d;
}

i {
  margin-left: 6px;
  font-size: 1.1rem;
}

@media (max-width: 500px) {
  .pomodoro-container {
    width: 95%;
    padding: 15px;
  }

  .break-container,
  .session-container {
    flex-direction: column;
    gap: 10px;
  }

  .control-buttons {
    flex-direction: column;
    gap: 10px;
  }

  #time-left {
    font-size: 2.5rem;
  }
}

@media (max-width: 350px) {
  .pomodoro-container {
    padding: 10px;
  }

  #time-left {
    font-size: 2rem;
  }

  .break-container button,
  .session-container button,
  .control-buttons button {
    padding: 8px 12px;
    font-size: 0.9rem;
  }
}
